import * as React from "react";

export const SpellCheckContext = React.createContext({
  word: null,
  set_word: () => {}
});

export const SpellCheckProvider = ({ children }) => {
  const [word, set_word] = React.useState("");
  const [suggestion, set_suggestion] = React.useState("look for nokempo");
  const value = { word, set_word, suggestion, set_suggestion };

  return (
    <SpellCheckContext.Provider value={value}>
      {children}
    </SpellCheckContext.Provider>
  );
};
