import * as React from "react";

import { SpellCheckContext } from "SETUP/app-context-manager/spell-check.setup";
export const SearchFieldHook = () => {
  const { word, set_word } = React.useContext(SpellCheckContext);
  // const { word, set_word } = value();

  const [content, setContent] = React.useState("");
  const [width, setWidth] = React.useState("");

  const span = React.useRef();

  React.useEffect(() => {
    setWidth(span.current.offsetWidth);
    set_word(() => content);
  }, [content, set_word]);

  const changeHandler = (e) => {
    const targetvalue = e.target.value;
    setContent(targetvalue);
  };

  return React.useMemo(() => ({ content, width, span, changeHandler }), [
    content,
    width
  ]);
};
