import * as React from "react";

// import { GoogleSpellCheck } from "COMPONENT/MODEL/google-spell-check/google-spell-check.com.model";
// import { SpellCheck } from "COMPONENT/MODEL/spell-check-js/spell-check-js.com.model";
import { SpellCheckContext } from "SETUP/app-context-manager/spell-check.setup";

export const SearchGroupHook = () => {
  const { word, suggestion, set_suggestion } = React.useContext(
    SpellCheckContext
  );

  let split = word.split(" ");
  let last_word = split[split.length - 1];
  set_suggestion(last_word);
  // SpellCheck(last_word);

  return React.useMemo(() => ({ last_word, word, suggestion }), [
    word,
    suggestion,
    last_word
  ]);
};
