import "./footer.styles.com.ui.scss";

const Footer = () => {
  return (
    <Box className="footer">
      <Card elevation={0}>
        <CardContent>
          <Typography variant="body1" color="text.secondary">
            Random Sheyt Work
          </Typography>
        </CardContent>
      </Card>
    </Box>
  );
};
